.tableWrap {
    display: table;
    width: 100vw;
    height: 100vh;
}

.tableCell {
    display: table-cell;
    vertical-align: middle;
}

.timerWrapper {
    position: relative;
    display: block;
    margin: auto;
    width: 75vw;
    height: 50vh;
    overflow: hidden;
}

.timerBlock {
    position: absolute;
    display: block;
    height: 98%;
    width: 98%;
    margin: auto;
    align-self: center;
    background: #292929;
    z-index: 1;
    top: 1%;
    left: 1%;
}

.timerBorder {
    position: absolute;
    display: block;
    top: -50%;
    left: -50%;
    height: 200%;
    width: 200%;
    overflow: hidden;
    background: #1f1f1f;
    z-index: -9;
    animation: borderAnim 3s linear infinite;
}

@keyframes borderAnim {
    to {
        background-position: 200% center;
    }
}

.timerContent {
    position: relative;
    width: 100%;
    color: #ffffff;
    text-align: center;
    vertical-align: middle;
}

.timeAndControls {
    margin: 0;
    position: relative;
    top: 50%;
    transform: translateY(50%);
}

.status {
    font-size: 30px;
    background: gray;
    width: 100%;
    text-align: center;
}

.clock {
    width: 100%;
    display: flex;
    font-size: 40px;
    font-weight: bold;
    justify-content: center;
}

.timeUp {
    transform: rotate(90deg);
    position: relative;
    bottom: 4px;
}

.timeDown {
    transform: rotate(90deg);
    position: relative;
    bottom: 4px;
}

.timeControls {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.minBtn {
    height: 20px;
}

.crements {
    position: absolute;
    width: 45px;
}

.sessionSet {
    background: #292929;
    color: white;
    width: 175px;
    font-size: 20px;
    height: 100px;
    position: absolute;
    top: 5%;
    left: 5%;
}

.minuteSet {
    position: relative;
    height: 50%;
}

.timeFloat {
    position: relative;
    z-index: 1;
    left: 15%;
}

.crementWid {
    width: 50%;
}

.crementFloat {
    position: relative;
    right: 15%;
}

.breakSet {
    background: #292929;
    color: white;
    width: 175px;
    font-size: 20px;
    height: 100px;
    position: absolute;
    top: 5%;
    right: 5%;
}

@media only screen and (min-width: 768px) {
    .timerWrapper {
        width: 50%;
        height: 40%;
    }

    .sessionSet {
        width: 250px;
        font-size: 25px;
        top: 10%;
        left: 15%;
    }

    .breakSet {
        width: 250px;
        font-size: 25px;
        top: 10%;
        right: 15%;
    }
}

@media only screen and (min-width: 1000px) {
    .timerWrapper {
        width: 30%;
    }
}
